/* jshint esversion:6 */

/**
 * Module for integrating Todo Tree with Notion.
 * Provides functionality to set credentials and sync TODOs with a Notion database.
 */

var vscode = require('vscode');

var { Client } = require('@notionhq/client');
var notionClient;
var NOTION_TOKEN_KEY = 'notionIntegrationToken';
var debug = console.log; // Default debug function, will be overridden if output channel is available

/**
 * Initializes the Notion integration module.
 * Sets up the debug function if provided.
 * @param {function} debugFunc - Optional debug function to log initialization status.
 */
function init(debugFunc) {
    if (debugFunc && typeof debugFunc === 'function') {
        debug = debugFunc;
    }
    debug("Notion integration initialization started.");
    debug("Notion integration initialized.");
}

/**
 * Prompts the user to set Notion integration credentials (API token and Database ID).
 * Stores the credentials securely and updates configuration.
 * @param {Object} context - The VSCode extension context for storing secrets.
 * @returns {Promise<boolean>} - Returns true if both token and database ID are provided, false otherwise.
 */
async function setCredentials(context) {
    try {
        debug("Setting Notion credentials...");
        // Get a workspace-specific key for storing the token
        const workspaceKey = getWorkspaceKey(context);
        const tokenKey = `${NOTION_TOKEN_KEY}_${workspaceKey}`;
        
        // Prompt for Notion API Token
        const token = await vscode.window.showInputBox({
            prompt: "Enter your Notion Integration Token for this project",
            placeHolder: "Token",
            password: true
        });

        if (token) {
            // Store the token securely using SecretStorage with workspace-specific key
            await context.secrets.store(tokenKey, token);
            vscode.window.showInformationMessage("Notion Integration Token saved securely for this project.");
            debug("Notion token saved successfully for workspace: " + workspaceKey);
        } else {
            vscode.window.showWarningMessage("No token provided. Notion integration will not work without a token for this project.");
            debug("No Notion token provided by user for workspace: " + workspaceKey);
            return false;
        }

        // Prompt for Notion Database ID (already in settings, but can be updated here if needed)
        const databaseId = await vscode.window.showInputBox({
            prompt: "Enter your Notion Database ID for Todo Sync in this project",
            placeHolder: "Database ID",
            value: vscode.workspace.getConfiguration('todo-tree.notion').get('databaseId', '')
        });

        if (databaseId) {
            // Update the database ID in settings, scoped to workspace
            await vscode.workspace.getConfiguration('todo-tree.notion').update('databaseId', databaseId, vscode.ConfigurationTarget.Workspace);
            vscode.window.showInformationMessage("Notion Database ID updated for this project.");
            debug("Notion Database ID updated to: " + databaseId + " for workspace: " + workspaceKey);
        } else {
            vscode.window.showWarningMessage("No Database ID provided. Using existing or default empty value for this project.");
            debug("No Notion Database ID provided, using existing value for workspace: " + workspaceKey);
        }

        return token && databaseId;
    } catch (error) {
        vscode.window.showErrorMessage("Failed to set Notion credentials: " + error.message);
        debug("Error setting Notion credentials: " + error.message);
        return false;
    }
}

/**
 * Retrieves the stored Notion credentials (token and database ID).
 * @param {Object} context - The VSCode extension context for accessing secrets.
 * @returns {Promise<Object>} - An object containing the token and databaseId.
 */
async function getCredentials(context) {
    try {
        const workspaceKey = getWorkspaceKey(context);
        const tokenKey = `${NOTION_TOKEN_KEY}_${workspaceKey}`;
        const token = await context.secrets.get(tokenKey);
        const databaseId = vscode.workspace.getConfiguration('todo-tree.notion').get('databaseId', '');
        debug("Retrieved Notion credentials for workspace " + workspaceKey + " (token: " + (token ? "present" : "not set") + ", databaseId: " + (databaseId || "not set") + ")");
        return { token, databaseId };
    } catch (error) {
        vscode.window.showErrorMessage("Failed to retrieve Notion credentials: " + error.message);
        debug("Error retrieving Notion credentials: " + error.message);
        return { token: null, databaseId: '' };
    }
}

/**
 * Fetches the current state of tasks from the Notion database.
 * @param {Object} context - The VSCode extension context for accessing credentials.
 * @returns {Promise<Array>} - A promise resolving to an array of task objects from Notion with custom ID mapping.
 */
async function fetchNotionState(context) {
    try {
        const { token, databaseId } = await getCredentials(context);
        if (!token || !databaseId) {
            vscode.window.showErrorMessage("Notion integration not configured. Please set credentials first.");
            debug("Notion credentials not configured for fetchNotionState.");
            return [];
        }

        // Initialize Notion client with the token
        notionClient = new Client({ auth: token });
        
        // Query the database for all pages
        const response = await notionClient.databases.query({
            database_id: databaseId
        });

        const tasks = response.results.map(page => {
            let taskText = "Untitled Task";
            let customId = "";
            let filePath = "";
            let lineNumber = undefined;
            let type = "";
            let status = "";
            try {
                if (page.properties.Name && page.properties.Name.title && page.properties.Name.title.length > 0) {
                    taskText = page.properties.Name.title[0].plain_text;
                }
                if (page.properties.ID && page.properties.ID.rich_text && page.properties.ID.rich_text.length > 0) {
                    customId = page.properties.ID.rich_text[0].plain_text;
                }
                if (page.properties["File Path"] && page.properties["File Path"].rich_text && page.properties["File Path"].rich_text.length > 0) {
                    filePath = page.properties["File Path"].rich_text[0].plain_text;
                }
                if (page.properties["Line Number"] && typeof page.properties["Line Number"].number === 'number') {
                    lineNumber = page.properties["Line Number"].number;
                }
                if (page.properties.Type && page.properties.Type.select && page.properties.Type.select.name) {
                    type = page.properties.Type.select.name;
                }
                if (page.properties.Status && page.properties.Status.select && page.properties.Status.select.name) {
                    status = page.properties.Status.select.name;
                }
            } catch (error) {
                debug("Error accessing task details for page " + page.id + ": " + error.message);
            }
            return {
                id: page.id,
                customId: customId,
                text: taskText,
                filePath: filePath,
                lineNumber: lineNumber,
                type: type,
                status: status
            };
        });

        debug("Fetched " + tasks.length + " tasks from Notion database.");
        vscode.window.showInformationMessage("Fetched " + tasks.length + " tasks from Notion.");
        return tasks;
    } catch (error) {
        if (error.message.includes("Could not find database with ID")) {
            vscode.window.showErrorMessage(`Failed to fetch Notion state: Database ID ${databaseId} not found. Ensure the database is shared with your integration.`, "Set Credentials").then(selection => {
                if (selection === "Set Credentials") {
                    setCredentials(context);
                }
            });
            debug("Error fetching Notion state: Database not found - " + error.message);
        } else if (error.message.includes("API token is invalid")) {
            vscode.window.showErrorMessage("Failed to fetch Notion state: Invalid API token. Please update your credentials.", "Set Credentials").then(selection => {
                if (selection === "Set Credentials") {
                    setCredentials(context);
                }
            });
            debug("Error fetching Notion state: Invalid API token - " + error.message);
        } else {
            vscode.window.showErrorMessage("Failed to fetch Notion state: " + error.message);
            debug("Error fetching Notion state: " + error.message);
        }
        return [];
    }
}

/**
 * Creates a new TODO item in Notion and returns the assigned ID.
 * @param {Object} context - The VSCode extension context for accessing credentials.
 * @param {Object} todo - The TODO item to create in Notion.
 * @returns {Promise<string|null>} - A promise resolving to the new TODO's ID or null if creation fails.
 */
async function createTodo(context, todo) {
    try {
        const { token, databaseId } = await getCredentials(context);
        if (!token || !databaseId) {
            vscode.window.showErrorMessage("Notion integration not configured. Please set credentials first.");
            debug("Notion credentials not configured for createTodo.");
            return null;
        }

        // Initialize Notion client if not already done
        if (!notionClient) {
            notionClient = new Client({ auth: token });
        }

        // Start with essential properties for TODO creation
        const properties = {
            Name: {
                title: [
                    {
                        text: {
                            content: todo.text
                        }
                    }
                ]
            },
            Type: {
                select: {
                    name: todo.type || "TODO"
                }
            },
            Status: {
                select: {
                    name: todo.status || "Not started"
                }
            }
        };

        // Fetch database properties to check which ones exist
        const dbProperties = await module.exports.listDatabaseProperties(context);
        let missingProperties = [];
        if (dbProperties) {
            if (dbProperties["File Path"] && todo.filePath) {
                properties["File Path"] = {
                    rich_text: [
                        {
                            text: {
                                content: todo.filePath || "Unknown"
                            }
                        }
                    ]
                };
            } else if (!dbProperties["File Path"]) {
                missingProperties.push("File Path");
            }
            if (dbProperties["Line Number"] && todo.lineNumber !== undefined) {
                properties["Line Number"] = {
                    number: todo.lineNumber || 0
                };
            } else if (!dbProperties["Line Number"]) {
                missingProperties.push("Line Number");
            }
            if (dbProperties["ID"] && todo.id) {
                properties["ID"] = {
                    rich_text: [
                        {
                            text: {
                                content: todo.id
                            }
                        }
                    ]
                };
            } else if (!dbProperties["ID"]) {
                missingProperties.push("ID");
            }
        } else {
            debug("Could not fetch database properties, using only essential properties for TODO creation.");
            vscode.window.showWarningMessage("Could not verify Notion database properties. Only essential fields will be set for the TODO.");
            missingProperties = ["File Path", "Line Number", "ID"];
        }

        // Create a new page in the specified database with checked properties
        const response = await notionClient.pages.create({
            parent: { database_id: databaseId },
            properties: properties
        });

        if (missingProperties.length > 0) {
            debug("Note: The following properties were not set for TODO creation due to schema mismatch: " + missingProperties.join(", ") + ". Available data - File Path: " + (todo.filePath || "Unknown") + ", Line Number: " + (todo.lineNumber || 0));
            vscode.window.showWarningMessage("TODO created without: " + missingProperties.join(", ") + ". Ensure these properties (especially 'Line Number') are defined in your Notion database schema to track TODO locations accurately.");
        }

        const newId = response.id;
        debug(`Created TODO in Notion with ID: ${newId} for text: ${todo.text}`);
        vscode.window.showInformationMessage(`Created TODO in Notion: ${todo.text}`);
        return newId;
    } catch (error) {
        if (error.message.includes("Could not find database with ID")) {
            vscode.window.showErrorMessage(`Failed to create TODO in Notion: Database ID ${databaseId} not found. Ensure the database is shared with your integration.`, "Set Credentials").then(selection => {
                if (selection === "Set Credentials") {
                    setCredentials(context);
                }
            });
            debug("Error creating TODO in Notion: Database not found - " + error.message);
        } else if (error.message.includes("API token is invalid")) {
            vscode.window.showErrorMessage("Failed to create TODO in Notion: Invalid API token. Please update your credentials.", "Set Credentials").then(selection => {
                if (selection === "Set Credentials") {
                    setCredentials(context);
                }
            });
            debug("Error creating TODO in Notion: Invalid API token - " + error.message);
        } else {
            vscode.window.showErrorMessage("Failed to create TODO in Notion: " + error.message);
            debug("Error creating TODO in Notion: " + error.message);
        }
        return null;
    }
}

/**
 * Updates an existing TODO item in Notion with the provided updates.
 * @param {Object} context - The VSCode extension context for accessing credentials.
 * @param {string} todoId - The ID of the TODO item to update (Notion page ID).
 * @param {Object} updates - The updates to apply to the TODO item.
 * @returns {Promise<boolean>} - A promise resolving to true if update succeeds, false otherwise.
 */
async function updateTodo(context, todoId, updates) {
    try {
        const { token } = await getCredentials(context);
        if (!token) {
            vscode.window.showErrorMessage("Notion integration not configured. Please set credentials first.");
            debug("Notion credentials not configured for updateTodo.");
            return false;
        }

        // Initialize Notion client if not already done
        if (!notionClient) {
            notionClient = new Client({ auth: token });
        }

        // Start with essential properties for TODO update
        const properties = {
            Name: {
                title: [
                    {
                        text: {
                            content: updates.text
                        }
                    }
                ]
            },
            Type: {
                select: {
                    name: updates.type || "TODO"
                }
            },
            Status: {
                select: {
                    name: updates.status || "Not started"
                }
            }
        };

        // Fetch database properties to check which ones exist
        const dbProperties = await module.exports.listDatabaseProperties(context);
        let missingProperties = [];
        if (dbProperties) {
            if (dbProperties["File Path"] && updates.filePath) {
                properties["File Path"] = {
                    rich_text: [
                        {
                            text: {
                                content: updates.filePath || "Unknown"
                            }
                        }
                    ]
                };
            } else if (!dbProperties["File Path"]) {
                missingProperties.push("File Path");
            }
            if (dbProperties["Line Number"] && updates.lineNumber !== undefined) {
                properties["Line Number"] = {
                    number: updates.lineNumber || 0
                };
            } else if (!dbProperties["Line Number"]) {
                missingProperties.push("Line Number");
            }
            if (dbProperties["ID"] && updates.id) {
                properties["ID"] = {
                    rich_text: [
                        {
                            text: {
                                content: updates.id
                            }
                        }
                    ]
                };
            } else if (!dbProperties["ID"]) {
                missingProperties.push("ID");
            }
        } else {
            debug("Could not fetch database properties, using only essential properties for TODO update.");
            vscode.window.showWarningMessage("Could not verify Notion database properties. Only essential fields will be updated for the TODO.");
            missingProperties = ["File Path", "Line Number", "ID"];
        }

        // Update the page with the given ID with checked properties
        const response = await notionClient.pages.update({
            page_id: todoId,
            properties: properties
        });

        if (missingProperties.length > 0) {
            debug("Note: The following properties were not updated for TODO due to schema mismatch: " + missingProperties.join(", ") + ". Available data - File Path: " + (updates.filePath || "Unknown") + ", Line Number: " + (updates.lineNumber || 0));
            vscode.window.showWarningMessage("TODO updated without: " + missingProperties.join(", ") + ". Ensure these properties (especially 'Line Number') are defined in your Notion database schema to track TODO locations accurately.");
        }

        debug(`Updated TODO in Notion with ID: ${todoId}`);
        vscode.window.showInformationMessage(`Updated TODO in Notion: ${updates.text}`);
        return true;
    } catch (error) {
        if (error.message.includes("API token is invalid")) {
            vscode.window.showErrorMessage("Failed to update TODO in Notion: Invalid API token. Please update your credentials.", "Set Credentials").then(selection => {
                if (selection === "Set Credentials") {
                    setCredentials(context);
                }
            });
            debug("Error updating TODO in Notion: Invalid API token - " + error.message);
        } else {
            vscode.window.showErrorMessage("Failed to update TODO in Notion: " + error.message);
            debug("Error updating TODO in Notion: " + error.message);
        }
        return false;
    }
}

/**
 * Deletes (archives) a TODO item in Notion.
 * @param {Object} context - The VSCode extension context for accessing credentials.
 * @param {string} todoId - The ID of the TODO item to delete.
 * @returns {Promise<boolean>} - A promise resolving to true if deletion succeeds, false otherwise.
 */
async function deleteTodo(context, todoId) {
    try {
        const { token } = await getCredentials(context);
        if (!token) {
            vscode.window.showErrorMessage("Notion integration not configured. Please set credentials first.");
            debug("Notion credentials not configured for deleteTodo.");
            return false;
        }

        // Initialize Notion client if not already done
        if (!notionClient) {
            notionClient = new Client({ auth: token });
        }

        // Archive the page in Notion to remove it from active view
        const response = await notionClient.pages.update({
            page_id: todoId,
            archived: true
        });

        debug(`Archived TODO in Notion with ID: ${todoId}`);
        vscode.window.showInformationMessage(`Archived TODO in Notion with ID: ${todoId}`);
        return true;
    } catch (error) {
        if (error.message.includes("API token is invalid")) {
            vscode.window.showErrorMessage("Failed to delete TODO in Notion: Invalid API token. Please update your credentials.", "Set Credentials").then(selection => {
                if (selection === "Set Credentials") {
                    setCredentials(context);
                }
            });
            debug("Error deleting TODO in Notion: Invalid API token - " + error.message);
        } else {
            vscode.window.showErrorMessage("Failed to delete TODO in Notion: " + error.message);
            debug("Error deleting TODO in Notion: " + error.message);
        }
        return false;
    }
}

/**
 * Synchronizes TODO items to Notion, handling creation, updates, and deletions in a batch-like process.
 * This function ensures that TODO IDs from the code are the single source of truth and prevents duplicate entries in Notion.
 * @param {Object} context - The VSCode extension context for accessing credentials.
 * @param {Array} todos - The current list of TODO items to sync from the code.
 * @param {Object} cachedTodos - The previously cached TODO items for comparison to detect changes.
 * @returns {Promise<Object>} - A promise resolving to an object with arrays of created, updated, and deleted TODOs.
 */
async function syncTodos(context, todos, cachedTodos = {}) {
    try {
        const { token, databaseId } = await getCredentials(context);
        if (!token || !databaseId) {
            vscode.window.showErrorMessage("Notion integration not configured. Please set credentials first.");
            debug("Notion credentials not configured for syncTodos.");
            return { created: [], updated: [], deleted: [] };
        }

        const created = [];
        const updated = [];
        const deleted = [];

        debug("Starting batch TODO sync with Notion...");
        vscode.window.showInformationMessage("Starting batch TODO sync with Notion...");

        // Fetch current state from Notion to determine which TODOs already exist
        const notionTasks = await fetchNotionState(context);
        
        // Create mappings for efficient lookup
        const customIdToPageId = {}; // Maps code ID to Notion page ID
        const notionTaskByPageId = {}; // Maps Notion page ID to task details for quick access
        notionTasks.forEach(task => {
            notionTaskByPageId[task.id] = task;
            if (task.customId) {
                customIdToPageId[task.customId] = task.id;
            }
        });

        // Process current TODOs from code for creation or update
        for (const todo of todos) {
            if (!todo.id) {
                debug(`Unexpected: TODO without ID for text: "${todo.text}". IDs should be generated in code.`);
                vscode.window.showErrorMessage(`Unexpected: TODO without ID for text: "${todo.text}". Please check the extension logic.`);
                continue;
            }

            if (customIdToPageId[todo.id]) {
                // Existing TODO in Notion (based on ID from code), update it using the Notion page ID
                const notionPageId = customIdToPageId[todo.id];
                debug(`TODO exists in Notion with ID from code: ${todo.id} (page ID: ${notionPageId}), checking for updates`);

                // Check if there are any changes to update (compare with Notion data, not cachedTodos)
                const notionTask = notionTaskByPageId[notionPageId];
                let needsUpdate = false;
                // Compare all relevant fields
                if (
                    notionTask.text !== todo.text ||
                    notionTask.filePath !== todo.filePath ||
                    notionTask.lineNumber !== todo.lineNumber ||
                    notionTask.type !== todo.type ||
                    notionTask.status !== todo.status
                ) {
                    needsUpdate = true;
                }

                if (needsUpdate) {
                    debug(`Updating TODO with ID: ${todo.id}, Line Number: ${todo.lineNumber}`);
                    const success = await updateTodo(context, notionPageId, {
                        text: todo.text,
                        type: todo.type,
                        status: todo.status,
                        filePath: todo.filePath,
                        lineNumber: todo.lineNumber,
                        id: todo.id
                    });
                    if (success) {
                        updated.push(todo);
                        debug(`Updated TODO with ID: ${todo.id} in Notion`);
                    } else {
                        debug(`Failed to update TODO with ID: ${todo.id} in Notion`);
                    }
                } else {
                    debug(`No changes detected for TODO with ID: ${todo.id}, skipping update`);
                }
            } else {
                // TODO has an ID in code but not found in Notion. Create new TODO in Notion.
                debug(`No TODO in Notion with ID: ${todo.id}. Creating new TODO in Notion.`);
                const newPageId = await createTodo(context, todo);
                if (newPageId) {
                    created.push(todo);
                    debug(`Created TODO with ID: ${todo.id} and page ID: ${newPageId} in Notion`);
                }
            }
        }

        // Optionally handle deletions: TODOs present in cachedTodos but not in current todos
        if (Object.keys(cachedTodos).length > 0) {
            debug("Checking for TODOs to delete in Notion...");
            const cachedIds = Object.keys(cachedTodos);
            for (const cachedId of cachedIds) {
                if (!customIdToPageId[cachedId]) {
                    // TODO with this ID does not exist in current todos, mark for deletion
                    const notionPageId = cachedTodos[cachedId];
                    deleted.push(notionPageId);
                    debug(`Marked TODO for deletion in Notion: ${notionPageId} (ID from code: ${cachedId})`);
                }
            }
        }

        // Perform deletion of marked TODOs in Notion
        for (const todoId of deleted) {
            const success = await deleteTodo(context, todoId);
            if (success) {
                debug(`Deleted TODO in Notion with ID: ${todoId}`);
            } else {
                debug(`Failed to delete TODO in Notion with ID: ${todoId}`);
            }
        }

        debug("Batch TODO sync with Notion completed.");
        vscode.window.showInformationMessage("Batch TODO sync with Notion completed.");
        return { created, updated, deleted };
    } catch (error) {
        vscode.window.showErrorMessage("Failed to sync TODOs with Notion: " + error.message);
        debug("Error syncing TODOs with Notion: " + error.message);
        return { created: [], updated: [], deleted: [] };
    }
}

/**
 * Retrieves the list of database properties for the Notion database.
 * @param {Object} context - The VSCode extension context for accessing credentials.
 * @returns {Promise<Object>} - A promise resolving to an object mapping property names to their types.
 */
async function listDatabaseProperties(context) {
    try {
        const { token, databaseId } = await getCredentials(context);
        if (!token || !databaseId) {
            vscode.window.showErrorMessage("Notion integration not configured. Please set credentials first.");
            debug("Notion credentials not configured for listDatabaseProperties.");
            return null;
        }

        // Initialize Notion client if not already done
        if (!notionClient) {
            notionClient = new Client({ auth: token });
        }

        // Retrieve the database information
        const response = await notionClient.databases.retrieve({
            database_id: databaseId
        });

        // Extract and return the properties schema
        const properties = response.properties;
        const propertySchema = {};
        for (const [key, value] of Object.entries(properties)) {
            propertySchema[key] = value.type;
        }

        debug("Retrieved database properties schema from Notion.");
        return propertySchema;
    } catch (error) {
        if (error.message.includes("API token is invalid")) {
            vscode.window.showErrorMessage("Failed to retrieve database properties: Invalid API token. Please update your credentials.", "Set Credentials").then(selection => {
                if (selection === "Set Credentials") {
                    setCredentials(context);
                }
            });
            debug("Error retrieving database properties: Invalid API token - " + error.message);
        } else {
            vscode.window.showErrorMessage("Failed to retrieve database properties: " + error.message);
            debug("Error retrieving database properties: " + error.message);
        }
        return null;
    }
}

/**
 * Gets a workspace-specific key for storing secrets.
 * @param {Object} context - The VSCode extension context.
 * @returns {string} - The workspace-specific key.
 */
function getWorkspaceKey(context) {
    const workspaceFolder = vscode.workspace.workspaceFolders && vscode.workspace.workspaceFolders.length > 0 ? vscode.workspace.workspaceFolders[0].uri.fsPath : "global";
    return workspaceFolder;
}

module.exports = {
    init,
    setCredentials,
    getCredentials,
    fetchNotionState,
    createTodo,
    updateTodo,
    deleteTodo,
    syncTodos,
    listDatabaseProperties
};
